cc.Class({
    extends: cc.Component,

    properties: {
        audioClip: {
            default: null,
            url: cc.AudioClip
        },
        _itemType: ItemType.None,
        itemType: {
            get() {
                return this._itemType;
            },
            set(value) {
                this._itemType = value;
            },
            type: ItemType
        }
    },
    playGetClip: function() {
        cc.audioEngine.play(this.audioClip, false, 1);
    }
});
